import React from 'react'
import {View, Image, Pressable} from 'react-native'
import {ScaledSheet, ms} from 'react-native-size-matters'
import {ICONS} from './images'
import {gray} from '../../constants'
import {Text} from '../Text'

interface GemProps {
  planNumber: number
  player?: {
    id: number
    uri: string | number
  }
  onPress?: () => void
}

const Gem: React.FC<GemProps> = ({planNumber, player, onPress}) => {
  const {container, gems, circle} = styles

  const source = () => {
    if (
      player &&
      typeof player.uri === 'number' &&
      player.uri >= 101 &&
      player.uri <= 106
    ) {
      return ICONS[player.uri - 101] // if your ICONS array contain 6 images for each number from 101 to 106
    } else if (player && typeof player.uri === 'string' && player.uri !== '') {
      return {uri: player.uri}
    } else {
      return null // Return null to show no image when there is no player on the position
    }
  }

  const isNumberVisible = !player && planNumber !== 68

  return (
    <Pressable onPress={onPress}>
      <View style={container}>
        {isNumberVisible ? (
          <View style={[circle, gems]}>
            <Text
              h={'h11'}
              title={planNumber !== 68 ? String(planNumber) : ' '}
              oneColor={gray}
            />
          </View>
        ) : source() ? (
          <Image style={gems} source={source()} testID="gem-image" />
        ) : null}
      </View>
    </Pressable>
  )
}

const styles = ScaledSheet.create({
  container: {
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 2,
  },
  gems: {
    width: ms(42, 0.5),
    height: ms(42, 0.5),
    borderRadius: ms(42, 0.5) / 2,
  },
  circle: {
    width: ms(44),
    height: ms(44),
    borderRadius: ms(44) / 2,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'transparent',
  },
})

export {Gem}
